	margin-top: 10%;
}
.labb {
	position: relative;
	/* top: 50%; */
	text-align: left;
	transform: translate(25%, 0%);
	height: 65vh;
	width: 75%;
	overflow: auto;
	z-index: 1;
}
.Rleft {
	width: 50vw;
	margin: 0;
	padding: 0;
	height: 100vh;
	/* background-color: aliceblue; */
	/* display: flex; */
	justify-content: center;
}
form {
	/* overflow: auto; */
}
.labb::-webkit-scrollbar {
	width: 10px;
	width: 5px;
}

/* Track */
.labb::-webkit-scrollbar-track {
	background: transparent;
}

/* Handle */
.labb::-webkit-scrollbar-thumb {
	background: #c4c4c4;
	height: 180px;
	border-radius: 32px;
}

/* Handle on hover */
.labb::-webkit-scrollbar-thumb:hover {
	background: #555;
}
